@mixin item-block {
    position: relative;
    width: 100%;
    color: white;
    margin: 0 0 20px;
    font-family: 'ralewaylight', Arial, sans-serif;
}

.fade-transition {
    @include transition(opacity, 0.4s);
}

@mixin title {
    font-family: "brandon_grotesquemedium", Arial, sans-serif;
    color: white;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: .2em;
}

#aq-composer-wrapper {
    .grid-container {
        margin: 0;
        padding: 0;
        max-width: none;
    }
    .composer-content,
    .composer-toolbar {
        padding: 20px 0;
        .composer-title {
            @include title;
            margin-bottom: 10px;
            text-align: center;
        }
        .composer-help {
            color: #9f9f9f;
            font-family: 'ralewaylight', Arial, sans-serif;
            margin-bottom: 10px;
        }
        .block-item {
            @include item-block;
            &.block-template {
                font-size: 14px;
                letter-spacing: .1em;
                border: 1px dashed white;
                text-align: center;
                background-color: $purple-dark;
                color: white;
                padding: 15px;
                @include zoomOnHover;
            }
            &:last-child {
                margin-bottom: 0;
            }
            &.image-text_render {
                .grid-45 {
                    &.prefix-5 {
                        padding-right: 0;
                    }
                    &.suffix-5 {
                        padding-left: 0;
                    }
                }
            }
            .block-task {
                opacity: 0;
                transition: opacity 0.7s;
                position: absolute;
                padding: 5px 5px 2px;
                top: 0;
                right: 0;
                z-index: 99999;
                .block-task-icon {
                    @include icomoon;
                    color: white;
                    &.task-move {
                        cursor: move;
                        &:before {
                            content: "\f047";
                        }
                    }
                    &.task-delete {
                        cursor: pointer;
                        &:before {
                            content: "\f014";
                        }
                    }
                }
            }
            &:hover {
                .block-task {
                    opacity: 1;
                }
            }
        }
    }
    .composer-content {
        background-color: $purple-darken;
        margin-top: 0;
        .block-item {
            padding-top: 10px;
            hr {
                border: none;
                border-top: 1px dotted white;
                margin: 40px 0px;
            }
        }
    }
    .composer-toolbar {
        text-align: center;
        padding: 20px 25px;
    }
}

.field-wrapper {
    margin-bottom: 15px;
    clear: both;
    position: relative;
    &:last-child {
        margin-bottom: 0;
    }
    .field-label-container {
        margin-bottom: 5px;
        @include title;
    }
    .composer-field {
        font-family: 'ralewayregular', Arial, sans-serif;
        font-size: 14px;
        color: #9f9f9f;
        letter-spacing: .05em;
        padding: 15px;
        line-height: initial;
        box-shadow: 0 0 0 0 transparent;
        border: none;
        border-radius: 1%;
        background: $purple-dark;
        line-height: 20px;
        width: 100%;
        @include zoomOnHover;
        &.select {
            cursor: pointer;
        }
        &.tinymce {
            min-height: 80px;
        }
        &:focus {
            outline: none;
        }
    }
}